.airdrop {
  margin-top: 80px;
  padding: 42px 16px 0px;
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.25);
  display: flex;
  flex-direction: column;
  align-items: center;
  background: radial-gradient(47.83% 75.18% at -2.6% 5.29%, rgba(245, 114, 85, 0.07) 0%, rgba(203, 245, 85, 0) 100%), #181D2B;

  @media all and (min-width: 1440px) {
    padding: 55px 97px 0px;
  }

  .title {
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 125%;
    @media all and (min-width: 1440px) {
      font-size: 45px;
    }

  }

  .text {
    font-family: Poppins;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 180%;
    text-align: center;
    color: #AFB2BF;
    margin-top: 16px;
    @media all and (min-width: 1440px) {
      font-size: 14px;
    }

  }

  .form {
    align-self: stretch;
    display: flex;
    flex-direction: column;
    margin-top: 24px;

    @media all and (min-width: 1440px) {
      margin-top: 40px;
      flex-direction: row;
      align-items: center;
      padding: 10px 10px 10px 30px;
      background: rgba(225, 219, 187, 0.1);
      border-radius: 8px;
    }


    input {
      background: rgba(225, 219, 187, 0.1);
      border-radius: 8px;
      border: 1px solid rgba(255, 255, 255, 0.17);
      padding: 20px 16px;
      font-family: Poppins;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 100%;

      @media all and (min-width: 1440px) {
        font-size: 14px;
        border-radius: 0px;
        background: none;
        border: none;
        flex-grow: 1;
        font-size: 14px;
      }

    }

    button {
      cursor: pointer;
      background: linear-gradient(94.2deg, #A6C17B 4.11%, #E1A56A 58.6%, #D5517F 106.2%), linear-gradient(90.86deg, #F951CA -7.66%, #4C53FF 95.98%);
      border-radius: 8px;
      border: none;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 19px 0;
      margin-top: 16px;

      font-family: Poppins;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 100%;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      @media all and (min-width: 1440px) {
        padding: 19px 45px;
        margin-top: 0px;
        margin-left: 16px;
      }

      &:hover {
        background: linear-gradient(89.64deg, #A6C17B 7.43%, #E1A56A 153.77%);
      }
    }


  }


  .timeBlock {
    align-self: stretch;
    background: linear-gradient(94.56deg, #AF6BEF -23.49%, #C7655F 53.03%, #D5517F 104.54%), #FFFFFF;
    border: 1px solid #525252;
    box-sizing: border-box;
    border-radius: 0px 0px 16px 16px;
    margin: 50px -17px -1px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 18px 0;
    @media all and (min-width: 1440px) {
      margin: 57px -98px -1px;
      flex-direction: row;
    }

    p {
      text-align: center;
    }

    p:first-child {
      font-family: Poppins;
      font-style: normal;
      font-weight: bold;
      font-size: 16px;
      line-height: 140%;
      @media all and (min-width: 1440px) {
        margin-right: 14px;
      }
    }

    p:last-child {
      span {
        font-family: Poppins;
        font-style: normal;
        font-weight: 600;
        font-size: 11px;
        line-height: 140%;
        text-transform: uppercase;
        white-space: pre-wrap;
        @media all and (min-width: 1440px) {
          font-size: 16px;
        }
      }

      span:nth-child(2),
      span:nth-child(5),
      span:nth-child(8) {
        color: #F5ACB5;
      }
    }

  }

}
